.compose {
    position: absolute;
    bottom: 0;
    right: 70px;
    min-width: 500px;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    box-shadow: 0px 0px 8px rgb(51 51 51/59%);
    background-color: #fff;
}
.compose .compose__header {
    display: flex;
    justify-content: space-between;
    background: #333;
    color: #fff;
    align-items: center;
    padding: 7px 15px;
}
.compose__headerLeft span {
    font-size: 14px;
}
.compose__header__right {
    display: flex;
    align-items: center;
}

.compose .compose__header .compose__header__right svg {
    font-size: 18px;
    margin-left: 10px;
    transition: 0.2s linear 0s;
    padding: 3px;
}
.compose__header__right svg:nth-child(2) path {
    transform: rotate(45deg);
    transform-origin: center;
}
.compose .compose__header .compose__header__right svg:hover {
    background: #545454;
    cursor: pointer;
}
.compose__body {
    display: flex;
}
.compose__bodyForm {
    display: flex;
    flex-direction: column;
}
.compose__bodyForm  input, textarea {
    width:100%;
    height:40px;
    outline: 0;
    border:0px;
    box-shadow:inset 0 -1px  0 0 rgb(100 121 143/12%);
    padding: 0px 10px;
}

.compose__bodyForm textarea {
    height:auto
}
.compose__footer {
    display: flex;
    justify-content: space-between;
    padding: 12px 10px;
}
.compose__footerLeft  button {
    display: flex;
    align-items: center;
    cursor: pointer;
    border:0;
    background-color:#297be6;
    outline: 0;
    color:#fff;
    font-size: 15px;
    padding:0px 0px 0px 15px !important;
    border-radius: 4px;
    letter-spacing: 1px;
}

.compose__footerLeft  button svg {
    border-left: 1px solid #00000052;
    margin-left: 15px;
    padding:6px 2px 6px 2px
}
.compose__footerRight svg {
    font-size: 20px;
    margin-left: 10px;
    border-radius: 3px;
    color:gray;
    padding: 3px;
    transition: 0.2 linear 0s;
    cursor: pointer;
}
.compose__footerRight svg:hover {
   background-color: #eee;
   color:#000;
}